html {
  scroll-behavior: smooth;
}
.pixel-perfect-logo,
input[type="checkbox"] {
  display: none;
}
@media only screen and (max-width: 600px) {
  .pixel-perfect-logo {
    display: block;
    font-size: 15px !important;
  }
  .my-name {
    display: none;
  }
  input[type="checkbox"] {
    position: absolute;
    right: 29px;
    z-index: 9999;
    height: 25px;
    width: 25px;
    opacity: 0;
    display: block;
  }
  input:checked + #header-nav {
    display: block !important;
  }
  .fa-facebook-f,
  .social-icons-dribbble .fa-linkedin {
    display: none;
  }
}
@media only screen and (max-width: 350px) {
  .pixel-perfect-logo {
    display: none;
  }
}

.hero-bg {
  background-image: url("../public/profile.jpeg");
}

.Typewriter__cursor {
  -webkit-animation: Typewriter-cursor 1s infinite;
  animation: Typewriter-cursor 1s infinite;
  margin-left: 1px;
}

@keyframes Typewriter-cursor {
  0% {
    opacity: 0;
  }
  
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
